// src/game/sceneMain.js
import * as THREE from 'three';
import { initCharacter } from './player/player.js';
import { createCamera } from './cameras/camera.js';
import { displayCharacterName, createPlayerUI } from './UI/playerUI.js';
import { setupPauseHandling } from './Utilities/pause.js';
import {
  getCombatMode,
  toggleCombatMode,
  startAttackMagic,
  stopAttackMagic,
  attackMagicAction,
  playAnimation
} from './player/playerUtilities/animationManager.js';
import { spawnProjectile, updateProjectiles, getProjectiles } from './combat/projectileManager.js';
import { EnemyManager } from './enemy/enemyUtilities/enemyManager.js';
import { clearTarget, updateTargetIndicator, getTarget } from './Utilities/targeting.js';
import {
  setupPointClickMovement,
  getDestination,
  clearDestination
} from './movement/pointClickMovement.js';
import { mixers } from './player/playerUtilities/characterLoader.js';
import { setupHDREnvironment } from './light.js';
import { checkCollisions } from './Utilities/collisionManager.js';
import { isMobileDevice } from './Utilities/deviceCheck.js';
import { createEscMenu, toggleEscMenu } from './UI/menuUI.js';
import { createMobileAttackButtons } from './UI/mobileUI.js';
import { setupWASDControls } from './movement/controls.js';
import { setupTargetingInput } from './Utilities/inputManager.js';
import { updatePlayerAppearance } from './player/playerUtilities/updatePlayerAppearance.js';
import grassVertexShader from '../shaders/mainGround/grassVertex.glsl';
import grassFragmentShader from '../shaders/mainGround/grassFragment.glsl';

let lastMagicAttack = 'ice';
const MANA_COST = 2;
const MOVE_SPEED = 0.05;
const ENEMY_SPEED = 0.03;

let selectedControlScheme = 'wasd'; // default control scheme
let movement = { forward: false, backward: false, left: false, right: false };

// We'll store cleanup functions so we can remove old listeners/controls as needed.
let cleanupWASD = null;
let cleanupPointClick = null;


// *** Rotation offset to align the model's forward with movement direction ***
// Adjust this value if the model is rotated differently (e.g., Math.PI for a 180 offset)
const rotationOffset = Math.PI /2;

export async function mainScene(characterData) {
  console.log('Main Game Scene Started');

  // Setup Scene & Renderer.
  const scene = new THREE.Scene();
  const renderer = new THREE.WebGLRenderer({ antialias: true });
  renderer.setSize(window.innerWidth, window.innerHeight);
  document.body.appendChild(renderer.domElement);

  setupHDREnvironment(scene, renderer);

  // Setup the ground with a custom grass shader
  const grassMaterial = new THREE.ShaderMaterial({
    vertexShader: grassVertexShader,
    fragmentShader: grassFragmentShader,
    uniforms: {
      time: { value: 0 },
      // Wind
      windDirection: { value: new THREE.Vector2(1.0, 0.0) },
      windFrequency: { value: 0.2 },
      windStrength: { value: 1.3 },
      // Footstep push
      playerPosition: { value: new THREE.Vector2(0, 0) },
      impactRadius: { value: 1.0 },
      impactStrength: { value: 8.0 },
      impactPosition: { value: new THREE.Vector3(0, 0, 0) },
      modelMatrix: { value: new THREE.Matrix4() }
    },
    side: THREE.DoubleSide
  });
  const groundGeometry = new THREE.PlaneGeometry(100, 100, 512, 512);
  const ground = new THREE.Mesh(groundGeometry, grassMaterial);
  ground.rotation.x = -Math.PI / 2;
  scene.add(ground);

  // Init player
  const player = await initCharacter(scene);
  player.userData = { tag: 'player', health: 100, mana: 100 };
  updatePlayerAppearance(characterData, player);

  // Enemies
  const enemyManager = new EnemyManager(scene, 3, 50);
  await enemyManager.spawnEnemies();
  const enemies = enemyManager.enemies;

  // UI
  displayCharacterName(characterData);
  const statusUI = createPlayerUI();
  statusUI.manaBarFill.style.width = `${(player.userData.mana / 100) * 100}%`;

  // Mobile
  if (isMobileDevice()) {
    createMobileAttackButtons(document.body, player);
  }

  // Pause Handling
  const pauseHandler = setupPauseHandling();

  // Create initial camera
  const selectedCameraStyle = 'runescape';
  let { camera, controls, updateCamera: updateCamFn } = createCamera(
    selectedCameraStyle,
    renderer,
    player.position
  );
  const targetCameraPosition = new THREE.Vector3();
  const followSpeed = 0.1;

  // Initialize controls based on selectedControlScheme
  if (selectedControlScheme === 'wasd') {
    cleanupWASD = setupWASDControls(movement);
  } else {
    cleanupPointClick = setupPointClickMovement(renderer, camera, ground);
  }

  // Setup targeting input
  setupTargetingInput({ scene, camera, player, enemyManager, renderer });

  // Menu callbacks
  function onCameraStyleChange(newStyle) {
    console.log('Camera style changed to:', newStyle);

    // Dispose old camera controls if available
    if (controls && controls.dispose) {
      controls.dispose();
    }

    // Create the new camera
    const newCameraSetup = createCamera(newStyle, renderer, player.position);
    camera = newCameraSetup.camera;
    controls = newCameraSetup.controls;
    updateCamFn = newCameraSetup.updateCamera;

    // If currently in "tap" mode, re-initialize pointClickMovement with the new camera
    if (selectedControlScheme === 'tap') {
      if (cleanupPointClick) {
        cleanupPointClick();
        cleanupPointClick = null;
      }
      cleanupPointClick = setupPointClickMovement(renderer, camera, ground);
    }
  }

  function onControlSchemeChange(newScheme) {
    selectedControlScheme = newScheme;
    console.log('Control scheme changed to:', newScheme);

    // Clean up old controls
    if (cleanupWASD) {
      cleanupWASD();
      cleanupWASD = null;
    }
    if (cleanupPointClick) {
      cleanupPointClick();
      cleanupPointClick = null;
    }

    // Initialize whichever scheme is selected
    if (selectedControlScheme === 'wasd') {
      cleanupWASD = setupWASDControls(movement);
    } else {
      cleanupPointClick = setupPointClickMovement(renderer, camera, ground);
    }
  }

  // Create the ESC menu and pass the callbacks
  const escMenu = createEscMenu(onCameraStyleChange, onControlSchemeChange);

  // Toggle menu on Escape
  document.addEventListener('keydown', (e) => {
    if (e.key === 'Escape') {
      toggleEscMenu(escMenu);
    }
  });

  // Magic Attack Key Listeners
  document.addEventListener('keydown', (event) => {
    if (event.key.toLowerCase() === 'z') {
      toggleCombatMode();
    }
    if (event.key === '1' && getCombatMode()) {
      lastMagicAttack = 'ice';
      if (player.userData.mana >= MANA_COST) {
        startAttackMagic();
      }
    }
    if (event.key === '2' && getCombatMode()) {
      lastMagicAttack = 'fire';
      if (player.userData.mana >= MANA_COST) {
        startAttackMagic();
      }
    }
  });
  document.addEventListener('keyup', (event) => {
    if ((event.key === '1' || event.key === '2') && getCombatMode()) {
      stopAttackMagic();
    }
  });

  // Ctrl+LeftClick Magic
  document.addEventListener('mousedown', (event) => {
    if (event.button === 0 && event.ctrlKey) {
      if (getCombatMode() && player.userData.mana >= MANA_COST) {
        startAttackMagic();
      }
    }
  });
  document.addEventListener('mouseup', (event) => {
    if (event.button === 0 && getCombatMode()) {
      stopAttackMagic();
    }
  });

  // For magic projectile timing
  let prevDistance = 0;
  let prevDelta = 0;
  let hasSpawnedProjectile = false;

  // Animation clock
  let lastTime = performance.now();

  // Helper for angle interpolation
  function shortAngleLerp(current, target, alpha) {
    let diff = (target - current) % (2 * Math.PI);
    if (diff < -Math.PI) diff += 2 * Math.PI;
    if (diff > Math.PI) diff -= 2 * Math.PI;
    return current + diff * alpha;
  }

  function animate() {
    const now = performance.now();
    const deltaTime = (now - lastTime) / 1000;
    lastTime = now;

    if (!pauseHandler.isPaused()) {
      // Update grass time uniform
      grassMaterial.uniforms.time.value = now * 0.001;

      // Update footstep/wind effect uniforms
      grassMaterial.uniforms.playerPosition.value.set(
        player.position.x,
        -player.position.z
      );
      grassMaterial.uniforms.impactPosition.value.copy(player.position);
      grassMaterial.uniforms.modelMatrix.value.copy(ground.matrixWorld);

      // Movement/Animation
      if (selectedControlScheme === 'wasd') {
        // WASD with camera orientation
        let cameraDir = new THREE.Vector3();
        camera.getWorldDirection(cameraDir);
        cameraDir.y = 0;
        cameraDir.normalize();

        let cameraRight = new THREE.Vector3();
        cameraRight.crossVectors(cameraDir, new THREE.Vector3(0, 1, 0)).normalize();

        let movementDir = new THREE.Vector3();
        if (movement.forward) movementDir.add(cameraDir);
        if (movement.backward) movementDir.sub(cameraDir);
        if (movement.left) movementDir.sub(cameraRight);
        if (movement.right) movementDir.add(cameraRight);

        if (movementDir.length() > 0) {
          movementDir.normalize();
          const desiredAngle = Math.atan2(movementDir.x, movementDir.z) + rotationOffset;
          player.rotation.y = shortAngleLerp(player.rotation.y, desiredAngle, 0.1);
          player.position.addScaledVector(movementDir, MOVE_SPEED);
          playAnimation('WalkForward', true);
        } else {
          playAnimation(getCombatMode() ? 'CombatIdle' : 'Idle', true);
        }
      } else {
        // 'tap' mode
        const dest = getDestination();
        if (dest) {
          const toDest = new THREE.Vector3().subVectors(dest, player.position);
          toDest.y = 0;
          if (toDest.length() < 0.1) {
            clearDestination();
            playAnimation(getCombatMode() ? 'CombatIdle' : 'Idle', true);
          } else {
            toDest.normalize();
            const desiredAngle = Math.atan2(toDest.x, toDest.z) + rotationOffset;
            // If no target is locked, rotate player toward destination
            if (!getTarget()) {
              player.rotation.y = shortAngleLerp(player.rotation.y, desiredAngle, 0.1);
            }
            player.position.addScaledVector(toDest, MOVE_SPEED);
            playAnimation('WalkForward', true);
          }
        } else {
          playAnimation(getCombatMode() ? 'CombatIdle' : 'Idle', true);
        }
      }

      // Update any active animations
      mixers.forEach((mixer) => mixer.update(deltaTime));

      // Update camera
      updateCamFn(camera, player, followSpeed, targetCameraPosition, controls);

      // Enemy updates
      enemyManager.update(deltaTime, ENEMY_SPEED);

      // Update targeting
      if (getTarget()) {
        updateTargetIndicator();
      }
      const target = getTarget();
      if (target && target.userData.health > 0) {
        const dir = new THREE.Vector3().subVectors(target.position, player.position);
        dir.y = 0;
        if (dir.length() > 0) {
          dir.normalize();
          const desiredAngle = Math.atan2(dir.x, dir.z) + rotationOffset;
          player.rotation.y = shortAngleLerp(player.rotation.y, desiredAngle, 0.1);
        }
      } else if (target && target.userData.health <= 0) {
        clearTarget(scene);
      }

      // Magic attack projectile spawn timing
      if (attackMagicAction && attackMagicAction.isRunning()) {
        if (attackMagicAction.time < 0.1) {
          prevDistance = 0;
          prevDelta = 0;
          hasSpawnedProjectile = false;
        }
        const wrist = player.getObjectByName('B_R_Hand');
        const root = player.getObjectByName('Motion');
        if (wrist && root) {
          const wristPos = new THREE.Vector3();
          const rootPos = new THREE.Vector3();
          wrist.getWorldPosition(wristPos);
          root.getWorldPosition(rootPos);
          const currentDistance = wristPos.distanceTo(rootPos);
          const currentDelta = currentDistance - prevDistance;
          const duration = attackMagicAction._clip.duration;
          // The condition below is an example of spawning once per animation cycle
          if (
            !hasSpawnedProjectile &&
            ((prevDelta > 0 && currentDelta < 0) ||
             attackMagicAction.time >= (duration - 0.1))
          ) {
            if (player.userData.mana >= MANA_COST) {
              player.userData.mana -= MANA_COST;
              statusUI.manaBarFill.style.width =
                `${(player.userData.mana / 100) * 100}%`;
              spawnProjectile(player, scene, lastMagicAttack, 'player');
            }
            hasSpawnedProjectile = true;
          }
          prevDelta = currentDelta;
          prevDistance = currentDistance;
        }
      } else {
        prevDistance = 0;
        prevDelta = 0;
        hasSpawnedProjectile = false;
      }

      // Collisions and projectiles
      checkCollisions(scene, player, enemies, getProjectiles);
      updateProjectiles(scene);

      // Render
      renderer.render(scene, camera);
    }
    requestAnimationFrame(animate);
  }
  animate();

  // Handle resizing
  window.addEventListener('resize', () => {
    camera.aspect = window.innerWidth / window.innerHeight;
    camera.updateProjectionMatrix();
    renderer.setSize(window.innerWidth, window.innerHeight);
  });

  console.log(`Character Name: ${characterData.name}`);
}
