// src/game/playerUtilities/characterLoader.js
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

let model, skeleton;
export let mixer = null; // Initialize mixer explicitly
export let mixers = [];

// Loads the character model, initializes the skeleton and mixer, and adds the model to the scene.
export function loadCharacter(scene, loader) {
    return new Promise((resolve, reject) => {
        loader.load(
            './modularCharacter.glb',
            (gltf) => {
                model = gltf.scene;
                let newScale = 1.0;
                model.scale.set(newScale, newScale, newScale);
                skeleton = model.getObjectByName('Motion'); // Adjust based on your skeleton name
                scene.add(model);

                // Create an AnimationMixer for this model
                mixer = new THREE.AnimationMixer(model);
                mixers.push(mixer);

                resolve(model);
            },
            undefined,
            (error) => {
                console.error('Error loading character:', error);
                reject(error);
            }
        );
    });
}

// Helper to access the skeleton if needed in other modules
export function getSkeleton() {
    return skeleton;
}
