// src/ui/enemyUI.js
import * as THREE from 'three';
/**
 * Creates a health bar above the enemy. 
 * The bar is a THREE.Sprite with a dynamic texture.
 */
export function createEnemyHealthBar(enemy) {
    const canvas = document.createElement('canvas');
    canvas.width = 128;
    canvas.height = 16;
    const context = canvas.getContext('2d');
    context.fillStyle = 'red';
    context.fillRect(0, 0, canvas.width, canvas.height);
  
    const texture = new THREE.CanvasTexture(canvas);
    const material = new THREE.SpriteMaterial({ map: texture, transparent: true });
    const sprite = new THREE.Sprite(material);
    sprite.scale.set(2, 0.25, 1);
    sprite.position.set(0, 3, 0); // Position above the enemy's head
    enemy.add(sprite);
  
    enemy.userData.healthBar = { sprite, canvas, context, texture };
  }
  
  /**
   * Updates the enemy's health bar based on its current health.
   */
  export function updateEnemyHealthBar(enemy) {
    const healthBar = enemy.userData.healthBar;
    if (!healthBar) return;
    const { canvas, context, texture } = healthBar;
    const healthPercent = Math.max(enemy.userData.health, 0) / 100; // or your max health
    context.clearRect(0, 0, canvas.width, canvas.height);
    context.fillStyle = 'grey';
    context.fillRect(0, 0, canvas.width, canvas.height);
    context.fillStyle = 'red';
    context.fillRect(0, 0, canvas.width * healthPercent, canvas.height);
    texture.needsUpdate = true;
  }
  