// src/game/cameras/firstPersonCamera.js
import * as THREE from 'three';
import { PointerLockControls } from 'three/examples/jsm/controls/PointerLockControls.js';

export function createFirstPersonCamera(renderer, initialTarget) {
  const camera = new THREE.PerspectiveCamera(
    75,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
  );
  // First-person: start at the player's head.
  camera.position.copy(initialTarget).add(new THREE.Vector3(0, 1.6, 0));
  const controls = new PointerLockControls(camera, renderer.domElement);

  // For first-person, the update function only needs to keep the camera at the player's head.
  function update(camera, player, followSpeed, targetCameraPosition) {
    const headOffset = new THREE.Vector3(0, 1.6, 0);
    camera.position.copy(player.position).add(headOffset);
    // PointerLockControls handle rotation, so no further updates here.
  }

  return { camera, controls, updateCamera: update };
}
