// src/UI/mobileUI.js

import { startAttackMagic, getCombatMode } from '../player/playerUtilities/animationManager.js';

/**
 * Creates big on-screen buttons for Fire and Ice attacks, appended to 'container'.
 * Only call this if you detect a mobile device or a small screen.
 */
export function createMobileAttackButtons(container, player) {
  const btnContainer = document.createElement('div');
  btnContainer.style.position = 'absolute';
  btnContainer.style.bottom = '10px';
  btnContainer.style.left = '50%';
  btnContainer.style.transform = 'translateX(-50%)';
  btnContainer.style.display = 'flex';
  btnContainer.style.gap = '10px';
  container.appendChild(btnContainer);

  const fireButton = document.createElement('button');
  fireButton.textContent = 'Fire Attack';
  fireButton.style.padding = '10px 15px';
  fireButton.addEventListener('click', () => {
    if (getCombatMode() && player.userData.mana >= 2) {
      // For example, pass "fire" if your system differentiates spells:
      // startAttackMagic('fire');
      startAttackMagic();
    }
  });
  btnContainer.appendChild(fireButton);

  const iceButton = document.createElement('button');
  iceButton.textContent = 'Ice Attack';
  iceButton.style.padding = '10px 15px';
  iceButton.addEventListener('click', () => {
    if (getCombatMode() && player.userData.mana >= 2) {
      // startAttackMagic('ice');
      startAttackMagic();
    }
  });
  btnContainer.appendChild(iceButton);
}
