// src/game/Utilities/inputManager.js
import * as THREE from 'three';
import { getEnemiesSortedByDistance, setTarget, clearTarget } from './targeting.js';

export function setupTargetingInput({ scene, camera, player, enemyManager, renderer }) {
  let targetIndex = -1;

  function onKeyDown(e) {
    if (e.key === 'Tab') {
      e.preventDefault(); // prevent browser tab switching
      const sortedEnemies = getEnemiesSortedByDistance(player, enemyManager.enemies);
      if (sortedEnemies.length === 0) {
        clearTarget(scene);
        targetIndex = -1;
        return;
      }
      // If no target is set, start at the first enemy.
      if (targetIndex === -1) {
        targetIndex = 0;
        setTarget(sortedEnemies[targetIndex], scene);
      } else if (targetIndex === sortedEnemies.length - 1) {
        // If we've reached the end of the list, clear the target and reset.
        clearTarget(scene);
        targetIndex = -1;
      } else {
        targetIndex++;
        setTarget(sortedEnemies[targetIndex], scene);
      }
    }
  }

  function onMouseDown(e) {
    if (e.button !== 0) return; // left click only
    const rect = renderer.domElement.getBoundingClientRect();
    const mouse = new THREE.Vector2(
      ((e.clientX - rect.left) / rect.width) * 2 - 1,
      -((e.clientY - rect.top) / rect.height) * 2 + 1
    );
    const raycaster = new THREE.Raycaster();
    raycaster.setFromCamera(mouse, camera);
    // Intersect against enemy objects (and their children)
    const intersects = raycaster.intersectObjects(enemyManager.enemies, true);
    if (intersects.length > 0) {
      const clickedEnemy = getRootEnemy(intersects[0].object);
      if (clickedEnemy && clickedEnemy.userData.health > 0) {
        setTarget(clickedEnemy, scene);
        targetIndex = -1; // reset cycling index
      }
    }
  }

  function getRootEnemy(obj) {
    let current = obj;
    while (current.parent) {
      if (current.userData && current.userData.tag === 'enemy') {
        return current;
      }
      current = current.parent;
    }
    return null;
  }

  window.addEventListener('keydown', onKeyDown);
  window.addEventListener('mousedown', onMouseDown);

  // Return a cleanup function to remove event listeners when needed.
  return () => {
    window.removeEventListener('keydown', onKeyDown);
    window.removeEventListener('mousedown', onMouseDown);
  };
}
