// src/game/player.js
import { loadCharacter } from './playerUtilities/characterLoader.js';
import { loadAnimation, playAnimation, updateCharacterMovement, initAttackListener } from './playerUtilities/animationManager.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { FBXLoader } from 'three/examples/jsm/Addons.js';

// Initializes the character by loading the model and animations.
async function initCharacter(scene) {
    const loader = new GLTFLoader();
    const fbxLoader = new FBXLoader();
    const player = await loadCharacter(scene, loader);
    
    // Load animations: Idle, WalkForward, CombatIdle, and AttackMagic.
    await loadAnimation('Idle', './Unarmed_Idle.glb', loader);
    await loadAnimation('WalkForward', './Relax_Walk_Forward.glb', loader);
    await loadAnimation('CombatIdle', './Armed_Idle.glb', loader);
    await loadAnimation('AttackMagic', './Armed_Cast_R_Attack_Hand1.glb', loader);
    // await loadAnimation('WalkForward', './Relax_Walk_Forward.fbx', fbxLoader);
    // await loadAnimation('CombatIdle', './Armed_Idle.fbx', fbxLoader);
    // await loadAnimation('AttackMagic', './Armed_Cast_R_Attack_Hand1.fbx', fbxLoader);
    
    // Initialize attack listener on the mixer.
    initAttackListener();
    
    // Start with Idle animation.
    playAnimation('Idle', true);
    
    return player;
}

export { initCharacter, updateCharacterMovement, playAnimation };
