// src/ui/playerUI.js


export function displayCharacterName(characterData) {
    // Create the welcome text container.
    const welcomeDiv = document.createElement('div');
    welcomeDiv.id = 'welcome-text';
    welcomeDiv.innerHTML = `Welcome, ${characterData.name}!`;
    
    // Style it similarly to RuneScape's intro screen.
    welcomeDiv.style.position = 'absolute';
    welcomeDiv.style.top = '10%';
    welcomeDiv.style.left = '50%';
    welcomeDiv.style.transform = 'translateX(-50%)';
    welcomeDiv.style.color = 'white';
    welcomeDiv.style.fontSize = '28px';
    welcomeDiv.style.fontFamily = 'Arial, sans-serif';
    welcomeDiv.style.padding = '10px 20px';
    welcomeDiv.style.backgroundColor = 'rgba(0, 0, 0, 0.6)';
    welcomeDiv.style.borderRadius = '5px';
    welcomeDiv.style.opacity = '1';
    welcomeDiv.style.transition = 'opacity 1s ease-out';
    
    document.body.appendChild(welcomeDiv);
  
    // Fade out after 3 seconds.
    setTimeout(() => {
      welcomeDiv.style.opacity = '0';
      // Optionally remove it from the DOM after fade-out completes.
      setTimeout(() => {
        if (welcomeDiv.parentNode) {
          welcomeDiv.parentNode.removeChild(welcomeDiv);
        }
      }, 1000); // Wait for the fade transition (1s)
    }, 3000);
  }
/**
 * Creates status bars for the player (e.g. health, mana).
 * Returns references for future updates.
 */
export function createPlayerUI() {
    const container = document.createElement('div');
    container.style.position = 'absolute';
    container.style.top = '10px';
    container.style.left = '10px';
    container.style.display = 'flex';
    container.style.flexDirection = 'column';
    container.style.gap = '10px';
    document.body.appendChild(container);
  
    // Health Bar
    const healthBarContainer = document.createElement('div');
    healthBarContainer.style.width = '200px';
    healthBarContainer.style.height = '20px';
    healthBarContainer.style.backgroundColor = '#555';
    const healthBarFill = document.createElement('div');
    healthBarFill.style.width = '100%';
    healthBarFill.style.height = '100%';
    healthBarFill.style.backgroundColor = 'red';
    healthBarContainer.appendChild(healthBarFill);
  
    // Mana Bar
    const manaBarContainer = document.createElement('div');
    manaBarContainer.style.width = '200px';
    manaBarContainer.style.height = '20px';
    manaBarContainer.style.backgroundColor = '#555';
    const manaBarFill = document.createElement('div');
    manaBarFill.style.width = '100%';
    manaBarFill.style.height = '100%';
    manaBarFill.style.backgroundColor = 'blue';
    manaBarContainer.appendChild(manaBarFill);
  
    container.appendChild(healthBarContainer);
    container.appendChild(manaBarContainer);
  
    return {
      healthBarFill,
      manaBarFill
    };
  }
  
  /**
   * Updates the player's health bar fill width.
   * @param {HTMLElement} healthBarFill - The health bar fill element.
   * @param {number} health - The current player health.
   * @param {number} maxHealth - The max player health.
   */
  export function updatePlayerHealthBar(healthBarFill, health, maxHealth) {
    const percent = Math.max(0, health) / maxHealth;
    healthBarFill.style.width = `${percent * 100}%`;
  }
  
  /**
   * Updates the player's mana bar fill width.
   */
  export function updatePlayerManaBar(manaBarFill, mana, maxMana) {
    const percent = Math.max(0, mana) / maxMana;
    manaBarFill.style.width = `${percent * 100}%`;
  }
  