// src/game/enemy/enemyUtilities/enemyBehaviour.js
import * as THREE from 'three';
import { playEnemyAnimation } from './enemyAnimationManager.js';

export function getRandomDestination(enemy) {
  const bbox = new THREE.Box3().setFromObject(enemy);
  const sphere = new THREE.Sphere();
  bbox.getBoundingSphere(sphere);
  const collisionRadius = sphere.radius;
  const maxDistance = collisionRadius * 3; // or more if you want bigger travel

  // We want at least a min distance so the enemy doesn't pick a super-close spot.
  const minDistance = 2.5; // or 2 or 3, tune as desired

  let destination;
  let attempts = 0;
  while (true) {
    const distance = THREE.MathUtils.randFloat(minDistance, maxDistance);
    const angle = Math.random() * 2 * Math.PI;
    const offset = new THREE.Vector3(Math.cos(angle), 0, Math.sin(angle)).multiplyScalar(distance);
    destination = enemy.position.clone().add(offset);
    destination.y = 0;
    destination.x = THREE.MathUtils.clamp(destination.x, -25, 25);
    destination.z = THREE.MathUtils.clamp(destination.z, -25, 25);

    // If we found a destination at least minDistance away, break
    const actualDist = enemy.position.distanceTo(destination);
    if (actualDist >= minDistance) break;

    attempts++;
    if (attempts > 10) {
      // fallback to just pick what we got if we can't find a big enough one
      break;
    }
  }

  return destination;
}

/**
 * Initializes random movement for the enemy.
 * We'll store setNewDestination on the enemy so the manager can call it.
 */
export function initRandomMovement(enemy) {
  function setNewDestination() {
    enemy.userData.destination = getRandomDestination(enemy);
    playEnemyAnimation('WalkForward', true, enemy);
    enemy.userData.currentAnimation = 'WalkForward';
  }
  
  // We'll not call setNewDestination here; the manager might call it initially
  // or we can do so once at spawn. It's optional.
  enemy.userData.setNewDestination = setNewDestination;
}
