// characterCreation/layout.js
export function applyOrientationLayout(uiPanel, modelPivot, previewCamera) {
    const isPortrait = window.innerHeight > window.innerWidth;
    if (isPortrait) {
      // For portrait: UI panel at bottom half, mesh centered in top half
      uiPanel.style.left = '0';
      uiPanel.style.top = '50%';
      uiPanel.style.width = '100%';
      uiPanel.style.height = '50%';
      if (modelPivot) {
        // Here we move the pivot so that the model (rotated about its bounding-box center)
        // appears centered in the top half. Adjust the Y value as needed.
        modelPivot.position.set(0, 1.8, -3);
      }
      previewCamera.position.set(0, 1.5, 4);
    } else {
      // For landscape: UI panel on left side, mesh on right side
      uiPanel.style.left = '0';
      uiPanel.style.top = '0';
      uiPanel.style.width = '350px';
      uiPanel.style.height = '100%';
      if (modelPivot) {
        // Position the pivot so the model appears centered on the right side.
        modelPivot.position.set(1.5, 1.5, -0.5);
      }
      previewCamera.position.set(0, 1.5, 4);
    }
  }
  