// src/game/collisionManager.js
import * as THREE from 'three';
import { updateEnemyHealthBar } from '../UI/enemyUI.js'; // If your enemy UI is at src/game/UI/enemyUI.js
import { playEnemyAnimation } from '../enemy/enemyUtilities/enemyAnimationManager.js';

/**
 * Checks collisions between projectiles and both the player and enemies.
 * @param {THREE.Scene} scene - The scene in which collisions occur.
 * @param {THREE.Object3D} player - The player object.
 * @param {Array<THREE.Object3D>} enemies - Array of enemy objects.
 * @param {Function} getProjectiles - A function returning the active projectiles array.
 */
export function checkCollisions(scene, player, enemies, getProjectiles) {
  const projectiles = getProjectiles();
  
  // Player bounding sphere.
  const playerBox = new THREE.Box3().setFromObject(player);
  const playerSphere = playerBox.getBoundingSphere(new THREE.Sphere());

  // For each enemy, check collisions with projectiles.
  enemies.forEach((enemy) => {
    // If the enemy is already dead, skip.
    if (enemy.userData.dead) return;

    const enemyBox = new THREE.Box3().setFromObject(enemy);
    const enemySphere = enemyBox.getBoundingSphere(new THREE.Sphere());

    for (let i = 0; i < projectiles.length; i++) {
      const proj = projectiles[i];
      const projPos = proj.mesh.position;

      if (proj.source === 'player') {
        // Player projectile  check collision with enemy.
        if (projPos.distanceTo(enemySphere.center) < enemySphere.radius) {
          enemy.userData.health -= 10;
          updateEnemyHealthBar(enemy); // from enemyUI
          scene.remove(proj.mesh);
          projectiles.splice(i, 1);
          i--;

          if (enemy.userData.health <= 0) {
            handleEnemyDeath(scene, enemy);
          }
        }
      } else if (proj.source === 'enemy') {
        // Enemy projectile  check collision with player.
        if (projPos.distanceTo(playerSphere.center) < playerSphere.radius) {
          player.userData.health -= 10;
          // Optionally update player's health bar if you have a function for it.
          scene.remove(proj.mesh);
          projectiles.splice(i, 1);
          i--;
        }
      }
    }
  });
}

/**
 * Handles enemy death by playing a death animation, then removing the enemy.
 * Disposes geometry and materials to free memory.
 * @param {THREE.Scene} scene
 * @param {THREE.Object3D} enemy
 */
function handleEnemyDeath(scene, enemy) {
  enemy.userData.dead = true;
  // For now, re-use 'WalkForward' as a placeholder for a death anim:
  playEnemyAnimation('WalkForward', false, enemy);

  setTimeout(() => {
    scene.remove(enemy);
    enemy.traverse((child) => {
      if (child.geometry) child.geometry.dispose();
      if (child.material) {
        if (Array.isArray(child.material)) {
          child.material.forEach((mat) => mat.dispose());
        } else {
          child.material.dispose();
        }
      }
    });
  }, 3000);
}
