// src/game/player/playerUtilities/updatePlayerAppearance.js
export function updatePlayerAppearance(characterData, player) {
  // Update skin tone
  let colorHex;
  switch (characterData.skinTone) {
    case 'light':
      colorHex = 0xffe0bd;
      break;
    case 'medium':
      colorHex = 0xffcd94;
      break;
    case 'dark':
      colorHex = 0xeac086;
      break;
    default:
      colorHex = 0xffe0bd;
  }
  
  // First, update the material color for all meshes
  player.traverse((child) => {
    if (child.isMesh && child.material) {
      child.material.color.setHex(colorHex);
    }
  });
  
  // Define the modular categories we expect
  const categories = [
    "head",
    "headCoverings",
    "hair",
    "eyebrows",
    "facialHair",
    "torso",
    "armUpperRight",
    "armUpperLeft",
    "armLowerRight",
    "armLowerLeft",
    "handRight",
    "handLeft",
    "hips",
    "legRight",
    "legLeft"
  ];
  
  // Hide all modular parts.
  // (Assumes that all modular meshes have names that start with "Chr_")
  player.traverse((child) => {
    if (child.isMesh && typeof child.name === "string" && child.name.indexOf("Chr_") === 0) {
      child.visible = false;
    }
  });
  
  // For each category, show the mesh whose name matches the selection.
  categories.forEach((cat) => {
    const selectedMeshName = characterData[cat];
    if (!selectedMeshName) return;
    const mesh = player.getObjectByName(selectedMeshName);
    if (mesh) {
      mesh.visible = true;
    }
  });
}
