// characterCreation/index.js
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';
import { PMREMGenerator } from 'three';
import { mainScene } from '/game/scene-main.js';
import { addModelDragListeners, addKeyboardListeners } from './controls.js';
import { applyOrientationLayout } from './layout.js';
import { centerModel, initializeCategoryIndexes, cyclePart, updatePreviewVisibility, updatePreviewSkinTone } from './model.js';
import { buildUIPanel } from './ui.js';
import { initialCharacterData, partCategories } from './constants.js';

let characterData = { ...initialCharacterData };
let alreadyLoaded = false;
let previewScene, previewCamera, previewRenderer;
let previewModel = null;
let modelPivot = null;
let envMap = null;
let previewAnimationFrameId = null;

let container, uiPanel;

export function characterCreationScene() {
  // MAIN CONTAINER
  container = document.createElement('div');
  container.style.position = 'absolute';
  container.style.top = '0';
  container.style.left = '0';
  container.style.width = '100%';
  container.style.height = '100%';
  container.style.overflow = 'hidden';
  document.body.appendChild(container);

  // WEBGL RENDERER
  previewRenderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
  previewRenderer.setSize(window.innerWidth, window.innerHeight);
  previewRenderer.domElement.style.position = 'absolute';
  previewRenderer.domElement.style.top = '0';
  previewRenderer.domElement.style.left = '0';
  previewRenderer.domElement.style.width = '100%';
  previewRenderer.domElement.style.height = '100%';
  previewRenderer.domElement.style.zIndex = '0';
  container.appendChild(previewRenderer.domElement);

  // SCENE & CAMERA
  previewScene = new THREE.Scene();
  previewCamera = new THREE.PerspectiveCamera(45, window.innerWidth / window.innerHeight, 0.1, 1000);
  previewCamera.position.set(0, 1.5, 4);
  previewScene.add(previewCamera);

  // HDR ENVIRONMENT
  const rgbeLoader = new RGBELoader();
  rgbeLoader.load('./aerodynamics_workshop.hdr', (hdrTex) => {
    hdrTex.mapping = THREE.EquirectangularReflectionMapping;
    const pmremGen = new PMREMGenerator(previewRenderer);
    pmremGen.compileEquirectangularShader();
    envMap = pmremGen.fromEquirectangular(hdrTex).texture;
    previewScene.environment = envMap;
    previewScene.background = envMap;
    hdrTex.dispose();
    pmremGen.dispose();
  });

  // LIGHTING
  const ambient = new THREE.AmbientLight(0xffffff, 0.3);
  previewScene.add(ambient);

  // UI PANEL
  uiPanel = document.createElement('div');
  uiPanel.style.position = 'absolute';
  uiPanel.style.backgroundColor = 'rgba(34,34,34,0.5)';
  uiPanel.style.color = '#fff';
  uiPanel.style.padding = '20px';
  uiPanel.style.boxSizing = 'border-box';
  uiPanel.style.overflowY = 'auto';
  uiPanel.style.zIndex = '1';
  container.appendChild(uiPanel);

  buildUIPanel(uiPanel, characterData, partCategories, () => {
    updatePreviewVisibility(previewModel, characterData);
  }, () => {
    initializeCategoryIndexes(characterData);
  });
  uiPanel.addEventListener('confirmGame', (e) => {
    if (previewAnimationFrameId) cancelAnimationFrame(previewAnimationFrameId);
    container.remove();
    loadMainGameScene(e.detail);
  });

  // LOAD MODEL & CREATE PIVOT
  const loader = new GLTFLoader();
  loader.load('./modularCharacter.glb', (gltf) => {
    previewModel = gltf.scene;
    modelPivot = new THREE.Object3D();
    centerModel(previewModel);
    modelPivot.add(previewModel);
    previewScene.add(modelPivot);

    initializeCategoryIndexes(characterData);
    updatePreviewSkinTone(previewModel, characterData.skinTone);
    updatePreviewVisibility(previewModel, characterData);
    applyOrientationLayout(uiPanel, modelPivot, previewCamera);
  });

  // CUSTOM DRAG & KEYBOARD ROTATION (rotate modelPivot)
  addModelDragListeners(previewRenderer, (deltaX, deltaY) => {
    if (!modelPivot) return;
    modelPivot.rotation.y += deltaX * 0.005;
    modelPivot.rotation.x += deltaY * 0.005;
  });
  addKeyboardListeners((key) => {
    if (!modelPivot) return;
    switch (key) {
      case 'ArrowUp':
        modelPivot.rotation.x -= 0.05;
        break;
      case 'ArrowDown':
        modelPivot.rotation.x += 0.05;
        break;
      case 'ArrowLeft':
        modelPivot.rotation.y += 0.05;
        break;
      case 'ArrowRight':
        modelPivot.rotation.y -= 0.05;
        break;
      default:
        break;
    }
  });

  // RENDER LOOP
  function animate() {
    previewAnimationFrameId = requestAnimationFrame(animate);
    previewRenderer.render(previewScene, previewCamera);
  }
  animate();

  window.addEventListener('resize', onWindowResize);
  onWindowResize();
}

function onWindowResize() {
  previewRenderer.setSize(window.innerWidth, window.innerHeight);
  previewCamera.aspect = window.innerWidth / window.innerHeight;
  previewCamera.updateProjectionMatrix();
  applyOrientationLayout(uiPanel, modelPivot, previewCamera);
}

// loadMainGameScene defined only here to avoid duplicate declaration
function loadMainGameScene(data) {
  if (!alreadyLoaded) {
    mainScene(data);
    alreadyLoaded = true;
  } else {
    console.log('Already loaded game!');
  }
}
