import { loadCharacterCreation } from "/game.js";

// Handle Start Menu
const startMenu = document.getElementById('start-menu');
const settingsMenu = document.getElementById('settings-menu');
const creditsMenu = document.getElementById('credits-menu');

// Possibly remove toggleMenuButton if its only for ESC toggling
// const toggleMenuButton = document.getElementById('toggle-menu');

const startButton = document.getElementById('start-button');
const settingsButton = document.getElementById('settings-button');
const creditsButton = document.getElementById('credits-button');

const backToMain = document.getElementById('back-to-main');
const backToMainCredits = document.getElementById('back-to-main-credits');

let gameInstance = false;  // Reference to the game script

// Event Listeners
startButton.addEventListener('click', startGame);
settingsButton.addEventListener('click', () => showMenu(settingsMenu));
creditsButton.addEventListener('click', () => showMenu(creditsMenu));

backToMain.addEventListener('click', () => showMenu(startMenu));
backToMainCredits.addEventListener('click', () => showMenu(startMenu));

// Remove or comment out the ESC toggling from script.js
// document.addEventListener('keydown', (event) => {
//     if (event.key === 'Escape') {
//         toggleGameMenu();
//     }
// });

function showMenu(menu) {
    // Hide all menus first
    startMenu.classList.add('hidden');
    settingsMenu.classList.add('hidden');
    creditsMenu.classList.add('hidden');

    // Show the selected menu
    menu.classList.remove('hidden');
}

// Start the game
function startGame() {
    console.log('Starting the game...');
    // Hide the start menu
    startMenu.classList.add('hidden');

    // Load the game script if not loaded yet
    if (!gameInstance) {
        loadCharacterCreation();
        gameInstance = true;
    }
}
