// src/game/controls.js
import * as THREE from 'three';

/**
 * Tap-to-move or click-to-move controls. (Unchanged, if you have it.)
 */
export function setupTapToMoveControls(renderer, camera, ground, setDestination) {
  function onPointerOrTouch(event) {
    let clientX, clientY;
    if (event.type.startsWith('touch')) {
      const touch = event.touches[0];
      clientX = touch.clientX;
      clientY = touch.clientY;
    } else {
      clientX = event.clientX;
      clientY = event.clientY;
    }

    const rect = renderer.domElement.getBoundingClientRect();
    const ndcX = ((clientX - rect.left) / rect.width) * 2 - 1;
    const ndcY = -(((clientY - rect.top) / rect.height) * 2 - 1);

    const raycaster = new THREE.Raycaster();
    raycaster.setFromCamera({ x: ndcX, y: ndcY }, camera);
    const intersects = raycaster.intersectObject(ground);
    if (intersects.length > 0) {
      const point = intersects[0].point;
      setDestination(point);
    }
  }

  renderer.domElement.addEventListener('mousedown', onPointerOrTouch);
  renderer.domElement.addEventListener('touchstart', onPointerOrTouch);

  // Return a cleanup function
  return () => {
    renderer.domElement.removeEventListener('mousedown', onPointerOrTouch);
    renderer.domElement.removeEventListener('touchstart', onPointerOrTouch);
  };
}

/**
 * Simple WASD controls for the player, removing arrow keys so
 * they can be used to orbit the camera instead.
 */
export function setupWASDControls(movement) {
  function onKeyDown(e) {
    if (e.key === 'w') movement.forward = true;
    if (e.key === 's') movement.backward = true;
    if (e.key === 'a') movement.left = true;
    if (e.key === 'd') movement.right = true;
  }
  function onKeyUp(e) {
    if (e.key === 'w') movement.forward = false;
    if (e.key === 's') movement.backward = false;
    if (e.key === 'a') movement.left = false;
    if (e.key === 'd') movement.right = false;
  }
  window.addEventListener('keydown', onKeyDown);
  window.addEventListener('keyup', onKeyUp);

  return () => {
    window.removeEventListener('keydown', onKeyDown);
    window.removeEventListener('keyup', onKeyUp);
  };
}
