// src/game/pointClickMovement.js
import * as THREE from 'three';

let destination = null;
// We'll store the event handler so we can remove it later.
let onClickHandler = null;

/**
 * Sets up a click listener on the renderer's DOM element so that when the ground is clicked,
 * the destination point is computed and stored.
 * Returns a cleanup function to remove the listener when switching cameras or controls.
 *
 * @param {THREE.WebGLRenderer} renderer - The renderer.
 * @param {THREE.Camera} camera - The active camera.
 * @param {THREE.Object3D} ground - The ground mesh.
 * @returns {Function} cleanup function
 */
export function setupPointClickMovement(renderer, camera, ground) {
  const raycaster = new THREE.Raycaster();
  const mouse = new THREE.Vector2();

  // Define the click handler using the current camera
  onClickHandler = (event) => {
    // Convert the mouse position to normalized device coordinates.
    mouse.x = (event.clientX / renderer.domElement.clientWidth) * 2 - 1;
    mouse.y = -(event.clientY / renderer.domElement.clientHeight) * 2 + 1;

    raycaster.setFromCamera(mouse, camera);
    const intersects = raycaster.intersectObject(ground);
    if (intersects.length > 0) {
      destination = intersects[0].point;
    }
  };

  // Add the event listener
  renderer.domElement.addEventListener('click', onClickHandler);

  // Return a cleanup function that removes this click listener
  return () => {
    renderer.domElement.removeEventListener('click', onClickHandler);
  };
}

/**
 * Returns the current destination (if any).
 */
export function getDestination() {
  return destination;
}

/**
 * Clears the current destination.
 */
export function clearDestination() {
  destination = null;
}
