// src/game/cameras/camera.js
import { createRunescapeCamera } from './runescapeCamera.js';
import { createDiabloCamera } from './diabloCamera.js';
import { createThirdPersonCamera } from './thirdPersonCamera.js';
import { createFirstPersonCamera } from './firstPersonCamera.js';

/**
 * Creates a camera based on the given style.
 * @param {string} style - 'runescape', 'diablo', 'thirdPerson', or 'firstPerson'.
 * @param {THREE.WebGLRenderer} renderer
 * @param {THREE.Vector3} initialTarget
 * @returns {Object} { camera, controls, updateCamera }
 */
export function createCamera(style, renderer, initialTarget) {
  switch (style) {
    case 'diablo':
      return createDiabloCamera(renderer, initialTarget);
    case 'thirdPerson':
      return createThirdPersonCamera(renderer, initialTarget);
    case 'firstPerson':
      return createFirstPersonCamera(renderer, initialTarget);
    case 'runescape':
    default:
      return createRunescapeCamera(renderer, initialTarget);
  }
}
