// src/game/cameras/thirdPersonCamera.js
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

export function createThirdPersonCamera(renderer, initialTarget) {
  const camera = new THREE.PerspectiveCamera(
    75,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
  );
  camera.position.set(0, 4, -8);
  const controls = new OrbitControls(camera, renderer.domElement);
  controls.enablePan = false;
  controls.enableDamping = true;
  controls.dampingFactor = 0.15;
  controls.target.copy(initialTarget);
  controls.update();

  function update(camera, player, followSpeed, targetCameraPosition) {
    const offset = new THREE.Vector3(0, 4, -8);
    targetCameraPosition.copy(player.position).add(offset);
    camera.position.lerp(targetCameraPosition, followSpeed);
    controls.target.copy(player.position);
    controls.update();
    camera.lookAt(player.position);
  }

  return { camera, controls, updateCamera: update };
}
