// characterCreation/model.js
import * as THREE from 'three';
import { partOptions, partCategories } from './constants.js';

export let categoryIndexes = {};

export function centerModel(model) {
  // Compute the bounding box and then get its center.
  const box = new THREE.Box3().setFromObject(model);
  const center = new THREE.Vector3();
  box.getCenter(center);
  // Adjust model position so that its center is at the pivot (0,0,0)
  model.position.sub(center);
}

export function initializeCategoryIndexes(characterData) {
  const currentGender = characterData.gender;
  partCategories.forEach((cat) => {
    const meshList = partOptions[currentGender][cat] || [];
    let idx = meshList.indexOf(characterData[cat]);
    if (idx < 0) idx = 0;
    categoryIndexes[cat] = idx;
  });
}

export function cyclePart(characterData, cat, direction) {
  const currentGender = characterData.gender;
  const meshList = partOptions[currentGender][cat] || [];
  if (!meshList.length) return;
  let idx = categoryIndexes[cat] || 0;
  idx = (idx + direction + meshList.length) % meshList.length;
  categoryIndexes[cat] = idx;
  characterData[cat] = meshList[idx];
}

export function updatePreviewVisibility(previewModel, characterData) {
  if (!previewModel) return;
  previewModel.traverse((child) => {
    if (child.isMesh) child.visible = false;
  });
  partCategories.forEach((cat) => {
    const meshName = characterData[cat];
    if (!meshName) return;
    const mesh = previewModel.getObjectByName(meshName);
    if (mesh) mesh.visible = true;
  });
}

export function updatePreviewSkinTone(previewModel, hexColor) {
  if (!previewModel) return;
  const colorInt = parseInt(hexColor.replace(/^#/, ''), 16);
  previewModel.traverse((child) => {
    if (child.isMesh && child.material) {
      child.material.color.setHex(colorInt);
    }
  });
}
