// src/game/playerUtilities/animationManager.js
import * as THREE from 'three';
import { mixer, mixers } from './characterLoader.js';

const animations = {}; // Store animation actions by name
const clock = new THREE.Clock();
let previousTime = 0;
let combatMode = false;
let attackActive = false;  // Track if attack input is active
let attackListenerAdded = false;

// In animationManager.js or a local helper
export let attackMagicAction = null;

// Combat mode getters/setters
export function getCombatMode() {
    return combatMode;
}
export function setCombatMode(mode) {
    combatMode = mode;
    if (combatMode) {
        // When switching to combat mode, revert to CombatIdle if not attacking
        if (!attackActive) playAnimation('CombatIdle', true);
    } else {
        playAnimation('Idle', true);
    }
}
export function toggleCombatMode() {
    setCombatMode(!combatMode);
}

// Loads an animation clip and stores its action in the animations object.
export function loadAnimation(name, file, loader) {
    return new Promise((resolve, reject) => {
        loader.load(
            file,
            (gltf) => {
                if (gltf.animations.length > 0) {
                    const clip = gltf.animations[0];
                    const action = mixer.clipAction(clip);
                    
                    // For WalkForward, adjust the clip to play only part of it.
                    if (name === 'WalkForward') {
                        const startFrame = 0;
                        const endFrame = 0.85;
                        const totalDuration = clip.duration;
                        const startTime = (startFrame / totalDuration) * clip.duration;
                        const endTime = (endFrame / totalDuration) * clip.duration;
                        
                        action.setLoop(THREE.LoopRepeat, Infinity);
                        action.clampWhenFinished = false;
                        action.time = startTime;
                        action._clip.duration = endTime - startTime;
                    }
                    animations[name] = action;
                }
                resolve();
            },
            undefined,
            (error) => {
                console.error(`Error loading animation ${name}:`, error);
                reject(error);
            }
        );
    });
}

// Plays the specified animation while fading out any other running animations.
export function playAnimation(name, loop = true) {
    if (!animations[name]) {
        console.warn(`Animation ${name} not found!`);
        return;
    }

    const action = animations[name];

    // Prevent restarting if already running.
    if (action.isRunning()) {
        // For attack animation, we update the loop mode if necessary.
        if (name === 'AttackMagic') {
            attackMagicAction = action; // So we can reference it elsewhere
            if (attackActive) {
                action.setLoop(THREE.LoopRepeat, Infinity);
            } else {
                action.setLoop(THREE.LoopOnce, 0);
            }
        }
        return;
    }

    console.log(`Playing animation: ${name}`);

    // Fade out all other animations.
    Object.values(animations).forEach((a) => {
        if (a !== action && a.isRunning()) {
            a.fadeOut(0.3);
        }
    });

    // Configure the action and play.
    action.reset();
    action.setEffectiveWeight(1.0);
    action.setEffectiveTimeScale(1.0);
    action.clampWhenFinished = false;

    if (name === 'AttackMagic') {
        if (attackActive) {
            action.setLoop(THREE.LoopRepeat, Infinity);
        } else {
            action.setLoop(THREE.LoopOnce, 0);
        }
    } else {
        action.setLoop(loop ? THREE.LoopRepeat : THREE.LoopOnce, loop ? Infinity : 0);
    }

    action.fadeIn(0.2);
    action.play();

    console.log(`Animation ${name} loop mode:`, action.loop);
}

// Initializes an event listener so that when AttackMagic finishes, we revert to CombatIdle.
export function initAttackListener() {
    if (attackListenerAdded) return;
    mixer.addEventListener('finished', (e) => {
       if (e.action === animations['AttackMagic']) {
           // When the attack animation finishes (after a one-shot loop), return to CombatIdle.
           if (!attackActive && getCombatMode()) {
               playAnimation('CombatIdle', true);
           }
       }
    });
    attackListenerAdded = true;
}

// Call this to trigger the start of the attack animation.
export function startAttackMagic() {
    if (!getCombatMode()) {
        console.warn("Cannot attack when not in combat mode.");
        return;
    }
    if (attackActive) return; // Already attacking
    attackActive = true;
    playAnimation('AttackMagic', true);
}

// Call this to stop the attack. This will switch the AttackMagic action to play one cycle.
export function stopAttackMagic() {
    if (!attackActive) return;
    attackActive = false;
    const attackAction = animations['AttackMagic'];
    if (attackAction) {
        // Switch the attack animation to play one final cycle.
        attackAction.setLoop(THREE.LoopOnce, 0);
    }
}

// Updates character movement and animation blending based on the provided movement object.
export function updateCharacterMovement(movement, model) {
    const elapsedTime = clock.getElapsedTime();
    const deltaTime = elapsedTime - previousTime;
    previousTime = elapsedTime;
  
    if (!mixers.length) return;
    mixers.forEach((m) => m.update(deltaTime));
  
    // If no movement object is provided, skip movement update.
    if (!movement) return;
  
    let direction = new THREE.Vector3();
    if (movement.forward) direction.z -= 1;
    if (movement.backward) direction.z += 1;
    if (movement.left) direction.x -= 1;
    if (movement.right) direction.x += 1;
  
    if (direction.length() > 0) {
      direction.normalize();
      const angle = Math.atan2(-direction.x, -direction.z);
      model.rotation.y = angle;
      model.position.addScaledVector(direction, 0.1);
  
      if (animations['WalkForward'] && !animations['WalkForward'].isRunning()) {
        console.log("Starting Walk Animation...");
        playAnimation('WalkForward', true);
      }
    } else {
      if (combatMode) {
        if (!animations['CombatIdle'].isRunning()) {
          console.log("Playing Combat Idle...");
          playAnimation('CombatIdle', true);
        }
      } else {
        if (!animations['Idle'].isRunning()) {
          console.log("Switching to Idle...");
          playAnimation('Idle', true);
        }
      }
    }
  }
  

export { animations };
