// src/game/enemy/enemyUtilities/enemyAnimationManager.js
import * as THREE from 'three';

const enemyAnimations = {}; // store loaded clips (shared across enemies)

export function loadEnemyAnimation(name, file, loader, mixer) {
  return new Promise((resolve, reject) => {
    loader.load(
      file,
      (gltf) => {
        if (gltf.animations.length > 0) {
          const clip = gltf.animations[0];
          if (name === 'WalkForward') {
            const startFrame = 0;
            const endFrame = 0.85;
            const totalDuration = clip.duration;
            const startTime = (startFrame / totalDuration) * clip.duration;
            const endTime = (endFrame / totalDuration) * clip.duration;
            clip.userData = { startTime, endTime };
          }
          enemyAnimations[name] = clip;
        }
        resolve();
      },
      undefined,
      (error) => {
        console.error(`Error loading enemy animation ${name}:`, error);
        reject(error);
      }
    );
  });
}

export function playEnemyAnimation(name, loop = true, enemy) {
  const mixer = enemy.userData.mixer;
  if (!mixer) {
    console.warn('No mixer for enemy');
    return false;
  }
  const clip = enemyAnimations[name];
  if (!clip) {
    console.warn(`Enemy animation ${name} not found!`);
    return false;
  }
  const newAction = mixer.clipAction(clip);

  // Identify the currently running action (if any)
  let currentAction = null;
  mixer._actions.forEach((action) => {
    if (action.isRunning() && action !== newAction) {
      currentAction = action;
    }
  });

  // If the action is already running, do nothing.
  if (newAction.isRunning()) return true;

  newAction.reset();
  newAction.setEffectiveWeight(1.0);
  newAction.setLoop(loop ? THREE.LoopRepeat : THREE.LoopOnce, loop ? Infinity : 0);
  if (name === 'WalkForward' && clip.userData) {
    newAction.time = clip.userData.startTime;
    newAction._clip.duration = clip.userData.endTime - clip.userData.startTime;
  }
  // If there is a current action, crossfade from it; otherwise, fade in normally.
  if (currentAction) {
    newAction.crossFadeFrom(currentAction, 0.3, false);
  } else {
    newAction.fadeIn(0.3);
  }
  
  newAction.play();
  return true;
}

export function updateEnemyAnimationsForEnemy(enemy, deltaTime) {
  if (enemy.userData.mixer) {
    enemy.userData.mixer.update(deltaTime);
  }
}
