// src/game/combat/projectileManager.js
import * as THREE from 'three';
// Import your custom GLSL shader files:
import iceVertexShader from '../../shaders/iceProjectile/iceProjectileVertex.glsl';
import iceFragmentShader from '../../shaders/iceProjectile/iceProjectileFragment.glsl';

const projectiles = [];
const trailParticles = []; // Global array for ice trail particles

// Define projectile types for scalability.
const PROJECTILE_TYPES = {
  ice: {
    // Use a simple sphere geometry.
    geometry: new THREE.SphereGeometry(0.1, 16, 16),
    // We don't use the standard material for ice; we use our custom ShaderMaterial.
    speed: 0.2,
    lifespan: 3000, // in milliseconds
  },
  fire: {
    geometry: new THREE.SphereGeometry(0.12, 16, 16),
    material: new THREE.MeshStandardMaterial({ color: 0xff5500 }),
    speed: 0.25,
    lifespan: 3000,
  },
};

/**
 * Spawns a projectile from the character's right hand.
 * @param {THREE.Object3D} character - The character (player or enemy).
 * @param {THREE.Scene} scene - The Three.js scene.
 * @param {string} type - The projectile type key (e.g., 'ice' or 'fire').
 * @param {string} source - Either 'player' or 'enemy'.
 */
export function spawnProjectile(character, scene, type = 'ice', source = 'player') {
  const config = PROJECTILE_TYPES[type];
  if (!config) {
    console.warn(`Projectile type "${type}" not found.`);
    return;
  }

  // Find the right hand's world position.
  const rightHand = character.getObjectByName('B_R_Hand');
  if (!rightHand) {
    console.warn('Right hand bone not found!');
    return;
  }
  const worldPos = new THREE.Vector3();
  rightHand.getWorldPosition(worldPos);

  // Determine the forward direction using the character's quaternion.
  const forward = new THREE.Vector3(-1, 0, 0);
  forward.applyQuaternion(character.quaternion).normalize();

  let mesh;
  if (type === 'ice') {
    const shaderMaterial = new THREE.ShaderMaterial({
      vertexShader: iceVertexShader,
      fragmentShader: iceFragmentShader,
      uniforms: {
        time: { value: 0 },
        opacity: { value: 1.0 },
        targetDirection: { value: new THREE.Vector3() }
      },
      transparent: true
    });
    // Set the targetDirection uniform.
    shaderMaterial.uniforms.targetDirection.value.copy(forward);
    
    mesh = new THREE.Mesh(config.geometry, shaderMaterial);
  } else {
    mesh = new THREE.Mesh(config.geometry, config.material.clone());
  }
  mesh.position.copy(worldPos);
  scene.add(mesh);

  // Use the forward vector for velocity.
  projectiles.push({
    mesh,
    velocity: forward.clone().multiplyScalar(config.speed),
    createdAt: performance.now(),
    lifespan: config.lifespan,
    source, // "player" or "enemy"
    lastTrailTime: 0
  });
}


function spawnIceTrailParticle(proj, scene) {
  // Create a small crystal particle.
  const geometry = new THREE.SphereGeometry(0.02, 8, 8);
  const material = new THREE.MeshBasicMaterial({
    color: 0x80dfff,
    transparent: true,
    opacity: 1.0
  });
  const particle = new THREE.Mesh(geometry, material);

  // Ensure the projectile's world matrix is up-to-date.
  proj.mesh.updateMatrixWorld();

  // If the projectile's geometry has a position attribute,
  // choose a random vertex from it.
  if (proj.mesh.geometry.attributes.position) {
    const posAttr = proj.mesh.geometry.attributes.position;
    const index = Math.floor(Math.random() * posAttr.count);
    let vertex = new THREE.Vector3().fromBufferAttribute(posAttr, index);
    // Transform the vertex from local to world space.
    vertex.applyMatrix4(proj.mesh.matrixWorld);
    // Offset the vertex slightly opposite the projectile's velocity to simulate a trailing effect.
    const trailOffset = proj.velocity.clone().normalize().multiplyScalar(-0.05);
    vertex.add(trailOffset);
    particle.position.copy(vertex);
  } else {
    // Fallback: use the projectile's position with a small random offset.
    particle.position.copy(proj.mesh.position);
    particle.position.x += (Math.random() - 0.5) * 0.05;
    particle.position.y += (Math.random() - 0.5) * 0.05;
    particle.position.z += (Math.random() - 0.5) * 0.05;
  }
  
  // Record the creation time for fading.
  particle.userData.creationTime = performance.now();
  scene.add(particle);
  trailParticles.push(particle);
}


/**
 * Spawns multiple ice trail particles (a burst) at the projectile's position.
 * @param {Object} proj - The projectile object.
 * @param {THREE.Scene} scene - The scene.
 */
function spawnIceTrailParticles(proj, scene) {
  const count = 8; // Spawn 8 particles per burst.
  for (let i = 0; i < count; i++) {
    // Randomize delay between 0 and 100ms for a more natural effect.
    setTimeout(() => {
      spawnIceTrailParticle(proj, scene);
    }, Math.random() * 100);
  }
}


/**
 * Updates all projectiles: moves them, updates shader uniforms, spawns trail particles,
 * and removes expired projectiles. Also updates and cleans up trail particles.
 * @param {THREE.Scene} scene - The scene in which the projectiles exist.
 */
export function updateProjectiles(scene) {
  const now = performance.now();
  // Update projectiles.
  for (let i = 0; i < projectiles.length; i++) {
    const proj = projectiles[i];
    proj.mesh.position.add(proj.velocity);
    if (proj.mesh.material instanceof THREE.ShaderMaterial) {
      proj.mesh.material.uniforms.time.value = (now - proj.createdAt) / 1000.0;
    }
    // For ice projectiles, spawn a burst of trail particles every 100ms.
    if (proj.source === 'player' && proj.mesh.material instanceof THREE.ShaderMaterial) {
      if (!proj.lastTrailTime || (now - proj.lastTrailTime) > 100) {
        spawnIceTrailParticles(proj, scene);
        proj.lastTrailTime = now;
      }
    }
    // Remove projectile if its lifespan is exceeded.
    if (now - proj.createdAt > proj.lifespan) {
      scene.remove(proj.mesh);
      projectiles.splice(i, 1);
      i--;
    }
  }

  // Update trail particles.
  for (let i = 0; i < trailParticles.length; i++) {
    const particle = trailParticles[i];
    const age = now - particle.userData.creationTime;
    const lifespan = 500; // Fully opaque for 500ms.
    const fadeDuration = 300; // Fade over the next 300ms.
    if (age > lifespan) {
      const fadeAge = age - lifespan;
      particle.material.opacity = Math.max(1 - (fadeAge / fadeDuration), 0);
      if (particle.material.opacity === 0) {
        scene.remove(particle);
        trailParticles.splice(i, 1);
        i--;
      }
    }
  }
}

/**
 * Returns the array of active projectiles.
 */
export function getProjectiles() {
  return projectiles;
}
