// src/game/enemy/enemy.js
import { loadEnemyCharacter } from './enemyUtilities/enemyCharacterLoader.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { loadEnemyAnimation, playEnemyAnimation } from './enemyUtilities/enemyAnimationManager.js';
import { initRandomMovement } from './enemyUtilities/enemyBehaviour.js';

async function initEnemy(scene) {
  const enemy = await loadEnemyCharacter(scene);
  const loader = new GLTFLoader();
  // Load animations using this enemy's mixer.
  await loadEnemyAnimation('Idle', './mainCharacter_idleAnimation.glb', loader, enemy.userData.mixer);
  await loadEnemyAnimation('WalkForward', './mainCharacter_walkAnimation.glb', loader, enemy.userData.mixer);
  playEnemyAnimation('Idle', true, enemy);
  initRandomMovement(enemy);
  return enemy;
}

export { initEnemy };
