// src/Utilities/deviceCheck.js

/**
 * Checks if the user is on a mobile device by looking at the user agent.
 * This is a simple approach and not 100% foolproof,
 * but it covers most common mobile user agents.
 */
export function isMobileDevice() {
    return /Mobi|Android|iPhone|iPad|iPod|Opera Mini|IEMobile|WPDesktop/i.test(navigator.userAgent);
  }
  