// src/game/Utilities/pause.js
let isPaused = false;

export function pauseGame() {
  isPaused = true;
  document.dispatchEvent(new Event('pauseGame'));
}

export function resumeGame() {
  isPaused = false;
  document.dispatchEvent(new Event('resumeGame'));
}

export function togglePause() {
  if (isPaused) {
    resumeGame();
  } else {
    pauseGame();
  }
}

export function setupPauseHandling() {
  document.addEventListener('keydown', (event) => {
    if (event.key === 'Escape') togglePause();
  });
  return {
    isPaused: () => isPaused,
    pauseGame,
    resumeGame,
    togglePause
  };
}
