// characterCreation/ui.js
import { cyclePart } from './model.js';

export function buildUIPanel(panel, characterData, partCategories, updateVisibilityCallback, initializeIndexesCallback) {
  panel.innerHTML = '';

  const title = document.createElement('h2');
  title.textContent = 'Create Your Character';
  title.style.marginTop = '0';
  panel.appendChild(title);

  // Name
  const nameLabel = document.createElement('label');
  nameLabel.textContent = 'Name:';
  panel.appendChild(nameLabel);
  const nameInput = document.createElement('input');
  nameInput.type = 'text';
  nameInput.value = characterData.name;
  nameInput.style.width = '100%';
  nameInput.addEventListener('input', () => {
    characterData.name = nameInput.value;
  });
  panel.appendChild(nameInput);

  // Gender
  const genderLabel = document.createElement('label');
  genderLabel.textContent = 'Gender:';
  panel.appendChild(genderLabel);
  const genderSelect = document.createElement('select');
  ['Male','Female'].forEach(g => {
    const option = document.createElement('option');
    option.value = g.toLowerCase();
    option.textContent = g;
    genderSelect.appendChild(option);
  });
  genderSelect.value = characterData.gender;
  genderSelect.addEventListener('change', () => {
    characterData.gender = genderSelect.value;
    initializeIndexesCallback();
    updateVisibilityCallback();
  });
  panel.appendChild(genderSelect);

  // Skin Tone
  const skinToneLabel = document.createElement('label');
  skinToneLabel.textContent = 'Skin Tone:';
  panel.appendChild(skinToneLabel);
  const skinToneInput = document.createElement('input');
  skinToneInput.type = 'color';
  skinToneInput.value = characterData.skinTone;
  skinToneInput.addEventListener('input', () => {
    characterData.skinTone = skinToneInput.value;
    updateVisibilityCallback();
  });
  panel.appendChild(skinToneInput);

  // Part Categories with arrow buttons
  partCategories.forEach((cat) => {
    const row = document.createElement('div');
    row.style.display = 'flex';
    row.style.flexDirection = 'row';
    row.style.alignItems = 'center';
    row.style.justifyContent = 'space-between';
    row.style.marginTop = '10px';

    const label = document.createElement('span');
    label.textContent = cat;
    label.style.flex = '1';
    row.appendChild(label);

    const leftBtn = document.createElement('button');
    leftBtn.textContent = '<';
    leftBtn.addEventListener('click', () => {
      cyclePart(characterData, cat, -1);
      updateVisibilityCallback();
    });
    row.appendChild(leftBtn);

    const rightBtn = document.createElement('button');
    rightBtn.textContent = '>';
    rightBtn.addEventListener('click', () => {
      cyclePart(characterData, cat, +1);
      updateVisibilityCallback();
    });
    row.appendChild(rightBtn);

    panel.appendChild(row);
  });

  // Confirm button dispatches a custom event so that index.js can listen for it
  const confirmBtn = document.createElement('button');
  confirmBtn.textContent = 'Confirm & Start Game';
  confirmBtn.style.marginTop = '20px';
  confirmBtn.addEventListener('click', () => {
    panel.dispatchEvent(new CustomEvent('confirmGame', { detail: characterData }));
  });
  panel.appendChild(confirmBtn);
}
