// src/game/cameras/runescapeCamera.js
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';

export function createRunescapeCamera(renderer, initialTarget) {
  const camera = new THREE.PerspectiveCamera(
    75,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
  );
  // Start slightly behind and above the player
  camera.position.set(0, 3, -6);

  const controls = new OrbitControls(camera, renderer.domElement);
  controls.enablePan = false;             // disable side-to-side panning
  controls.enableDamping = true;          // smooth orbit
  controls.dampingFactor = 0.1;
  controls.minPolarAngle = Math.PI / 6;   // tilt limits
  controls.maxPolarAngle = Math.PI / 2;
  controls.enableZoom = true;             // allow mouse-wheel zoom
  controls.enableRotate = true;           // allow mouse-drag to orbit

  // Restrict zoom distance
  controls.minDistance = 2;
  controls.maxDistance = 20;

  // Focus on the player's initial position
  controls.target.copy(initialTarget);
  controls.update();

  // ---- ARROW-KEY ORBITING ----
  // We adjust OrbitControls' internal spherical delta so the arrow keys work.
  function onKeyDown(event) {
    const orbitSpeed = 0.05;
    let usedKey = true;
    switch (event.key) {
      case 'ArrowLeft':
        controls._sphericalDelta.theta -= orbitSpeed;
        break;
      case 'ArrowRight':
        controls._sphericalDelta.theta += orbitSpeed;
        break;
      case 'ArrowUp':
        controls._sphericalDelta.phi -= orbitSpeed;
        break;
      case 'ArrowDown':
        controls._sphericalDelta.phi += orbitSpeed;
        break;
      default:
        usedKey = false;
        break;
    }
    if (usedKey) {
      event.preventDefault();
    }
  }
  window.addEventListener('keydown', onKeyDown);

  // Store previous target position for translating the camera
  let previousTarget = new THREE.Vector3().copy(controls.target);

  /**
   * The update function:
   * - Smoothly translates the camera (and its orbit target) based on the player's movement.
   * - This ensures the camera follows the player (i.e. moves with them) while still preserving
   *   the users chosen orbit and zoom settings.
   *
   * @param {THREE.Camera} camera 
   * @param {THREE.Object3D} player - The player object with its current position.
   * @param {number} followSpeed - A value between 0 and 1 controlling the smoothing.
   * @param {THREE.Vector3} targetCameraPosition - (Unused in this version)
   */
  function update(camera, player, followSpeed, targetCameraPosition) {
    // Compute a smoothed (lerped) target position between the previous target and the player's position.
    const newTarget = player.position;
    const lerpedTarget = previousTarget.clone().lerp(newTarget, followSpeed);

    // Compute the delta (translation) needed from the previous target.
    const delta = new THREE.Vector3().subVectors(lerpedTarget, previousTarget);

    // Translate both the camera and the orbit target by the delta.
    controls.target.add(delta);
    camera.position.add(delta);

    // Update previous target for the next frame.
    previousTarget.copy(lerpedTarget);

    // Let OrbitControls process any orbit/zoom changes.
    controls.update();
  }

  // Clean up event listeners if needed.
  function dispose() {
    window.removeEventListener('keydown', onKeyDown);
    if (typeof controls.dispose === 'function') {
      controls.dispose();
    }
  }

  return {
    camera,
    controls,
    updateCamera: update,
    dispose
  };
}
