// src/game/targeting.js
import * as THREE from 'three';

let currentTarget = null;
let targetIndicator = null;

export function getTarget() {
  return currentTarget;
}

export function clearTarget(scene) {
  currentTarget = null;
  if (targetIndicator) {
    scene.remove(targetIndicator);
    targetIndicator = null;
  }
}

export function setTarget(enemy, scene) {
  currentTarget = enemy;
  if (!targetIndicator) {
    const geometry = new THREE.RingGeometry(1.1, 1.3, 32);
    const material = new THREE.MeshBasicMaterial({ color: 0xff0000, side: THREE.DoubleSide });
    targetIndicator = new THREE.Mesh(geometry, material);
    targetIndicator.rotation.x = -Math.PI / 2;
    scene.add(targetIndicator);
  }
  // Set the indicator to the enemy's current position.
  targetIndicator.position.copy(enemy.position);
  targetIndicator.position.y += 0.1;
}

// This function will update the indicator each frame.
export function updateTargetIndicator() {
  if (currentTarget && targetIndicator) {
    targetIndicator.position.copy(currentTarget.position);
    targetIndicator.position.y += 0.1;
  }
}

export function getEnemiesSortedByDistance(player, enemies) {
  const aliveEnemies = enemies.filter(e => e.userData && e.userData.health > 0);
  aliveEnemies.sort((a, b) => {
    const distA = a.position.distanceTo(player.position);
    const distB = b.position.distanceTo(player.position);
    return distA - distB;
  });
  return aliveEnemies;
}
