// src/game/enemy/enemyUtilities/enemyCharacterLoader.js
import * as THREE from 'three';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';

export function loadEnemyCharacter(scene) {
  return new Promise((resolve, reject) => {
    const loader = new GLTFLoader();
    loader.load(
      './mainCharacter_tPose.glb',
      (gltf) => {
        const enemyModel = gltf.scene;
        // If enemy.userData already exists, don't overwrite it.
        enemyModel.userData = enemyModel.userData || {};
        enemyModel.userData.tag = 'enemy';
        enemyModel.userData.health = 100;
        enemyModel.userData.mana = 100;
        // Create and attach a unique mixer.
        enemyModel.userData.mixer = new THREE.AnimationMixer(enemyModel);
        scene.add(enemyModel);
        resolve(enemyModel);
      },
      undefined,
      (error) => {
        console.error('Error loading enemy character:', error);
        reject(error);
      }
    );
  });
}
