// src/game/cameras/diabloCamera.js
import * as THREE from 'three';

export function createDiabloCamera(renderer, initialTarget) {
  const camera = new THREE.PerspectiveCamera(
    75,
    window.innerWidth / window.innerHeight,
    0.1,
    1000
  );
  // Diablo style: a fixed isometric view.
  camera.position.set(10, 15, 10);
  camera.lookAt(initialTarget);

  // For Diablo style, the camera might not follow the player dynamically;
  // but if you want a slight follow effect, you can define one.
  function update(camera, player, followSpeed, targetCameraPosition) {
    // For example, we keep the isometric offset relative to the player.
    const offset = new THREE.Vector3(10, 15, 10);
    targetCameraPosition.copy(player.position).add(offset);
    camera.position.lerp(targetCameraPosition, followSpeed);
    camera.lookAt(player.position);
  }

  return { camera, controls: null, updateCamera: update };
}
