// src/game/UI/menuUI.js
import { resumeGame } from '../Utilities/pause.js';

export function createEscMenu(onCameraStyleChange, onControlSchemeChange) {
    const escMenu = document.createElement('div');
    escMenu.id = 'esc-menu';
    escMenu.style.position = 'absolute';
    escMenu.style.top = '0';
    escMenu.style.left = '0';
    escMenu.style.width = '100%';
    escMenu.style.height = '100%';
    escMenu.style.backgroundColor = 'rgba(0, 0, 0, 0.7)';
    escMenu.style.display = 'none';
    escMenu.style.flexDirection = 'column';
    escMenu.style.alignItems = 'center';
    escMenu.style.justifyContent = 'center';
    escMenu.style.zIndex = '999';
  
    const menuContainer = document.createElement('div');
    menuContainer.style.backgroundColor = '#222';
    menuContainer.style.padding = '20px';
    menuContainer.style.borderRadius = '10px';
    menuContainer.style.display = 'flex';
    menuContainer.style.flexDirection = 'column';
    menuContainer.style.alignItems = 'center';
    menuContainer.style.gap = '15px';
  
    const title = document.createElement('h2');
    title.textContent = 'Settings';
    title.style.color = 'white';
    menuContainer.appendChild(title);
  
    // (Volume slider and difficulty omitted for brevity)
  
    // Camera Style select
    const cameraLabel = document.createElement('label');
    cameraLabel.style.color = 'white';
    cameraLabel.textContent = 'Camera Style: ';
    const cameraSelect = document.createElement('select');
    const cameraStyles = [
      { value: 'runescape', label: 'Runescape' },
      { value: 'diablo', label: 'Diablo' },
      { value: 'thirdPerson', label: 'Third Person' },
      { value: 'firstPerson', label: 'First Person' }
    ];
    cameraStyles.forEach(s => {
      const option = document.createElement('option');
      option.value = s.value;
      option.textContent = s.label;
      cameraSelect.appendChild(option);
    });
    cameraLabel.appendChild(cameraSelect);
    menuContainer.appendChild(cameraLabel);
  
    // Control Scheme select
    const controlLabel = document.createElement('label');
    controlLabel.style.color = 'white';
    controlLabel.textContent = 'Control Scheme: ';
    const controlSelect = document.createElement('select');
    const controlOptions = [
      { value: 'wasd', label: 'WASD/Game Controller' },
      { value: 'tap', label: 'Point/Tap Click' }
    ];
    controlOptions.forEach(opt => {
      const option = document.createElement('option');
      option.value = opt.value;
      option.textContent = opt.label;
      controlSelect.appendChild(option);
    });
    controlLabel.appendChild(controlSelect);
    menuContainer.appendChild(controlLabel);
  
    // Call callbacks on changes.
    cameraSelect.addEventListener('change', () => {
      if (onCameraStyleChange) {
        onCameraStyleChange(cameraSelect.value);
      }
    });
    controlSelect.addEventListener('change', () => {
      if (onControlSchemeChange) {
        onControlSchemeChange(controlSelect.value);
      }
    });
  
    // Back button: when clicked, hide the menu and resume the game.
    const backButton = document.createElement('button');
    backButton.textContent = 'Back';
    backButton.style.padding = '10px 20px';
    backButton.addEventListener('click', () => {
      escMenu.style.display = 'none';
      resumeGame();
    });
    menuContainer.appendChild(backButton);
  
    escMenu.appendChild(menuContainer);
    document.body.appendChild(escMenu);
  
    return escMenu;
}
  
export function toggleEscMenu(escMenu) {
  escMenu.style.display = (escMenu.style.display === 'none') ? 'flex' : 'none';
}
