// src/game/enemy/enemyUtilities/enemyManager.js
import * as THREE from 'three';
import { initEnemy } from '../enemy.js';
import { playEnemyAnimation } from './enemyAnimationManager.js';
import { createEnemyHealthBar } from '../../UI/enemyUI.js';
import { updateEnemyAnimationsForEnemy } from './enemyAnimationManager.js';

export class EnemyManager {
  constructor(scene, numberOfEnemies, groundSize = 50) {
    this.scene = scene;
    this.numberOfEnemies = numberOfEnemies;
    this.enemies = [];
    this.groundSize = groundSize;
  }

  async spawnEnemies() {
    for (let i = 0; i < this.numberOfEnemies; i++) {
      const enemy = await initEnemy(this.scene);
      enemy.position.set(
        THREE.MathUtils.randFloatSpread(this.groundSize),
        0,
        THREE.MathUtils.randFloatSpread(this.groundSize)
      );
      enemy.userData.tag = 'enemy';
      enemy.userData.health = 100;
      enemy.userData.mana = 100;
      enemy.userData.currentAnimation = null;
      createEnemyHealthBar(enemy);

      // Start with a new destination
      if (enemy.userData.setNewDestination) {
        enemy.userData.setNewDestination();
      } else {
        console.warn('No setNewDestination function on enemy');
      }
      this.enemies.push(enemy);
    }
  }

  update(deltaTime, speed) {
    this.enemies.forEach((enemy) => {
      if (enemy.userData.destination) {
        const toDest = new THREE.Vector3().subVectors(enemy.userData.destination, enemy.position);
        toDest.y = 0;
        const dist = toDest.length();
        if (dist < 0.05) {
          // Very close: consider fully arrived
          enemy.userData.destination = null;
          if (enemy.userData.currentAnimation !== 'Idle') {
            playEnemyAnimation('Idle', true, enemy);
            enemy.userData.currentAnimation = 'Idle';
          }
          // Schedule new destination after 2 seconds
          if (!enemy.userData.newDestScheduled) {
            enemy.userData.newDestScheduled = true;
            setTimeout(() => {
              if (enemy.userData.setNewDestination) {
                enemy.userData.setNewDestination();
              }
              enemy.userData.newDestScheduled = false;
            }, 2000);
          }
        } else {
          // Not arrived yet
          toDest.normalize();
  
          // Scale speed so we slow down as we approach
          // e.g. if dist=2, factor=1; if dist=0.2, factor=0.1
          // This ensures a smooth approach
          const factor = THREE.MathUtils.clamp(dist, 0.05, 1.0); // or tune
          const approachSpeed = speed * factor; // or speed*(dist/1.0)
  
          // Rotate enemy toward destination
          const desiredAngle = Math.atan2(toDest.x, toDest.z);
          enemy.rotation.y = this.lerpAngle(enemy.rotation.y, desiredAngle, 0.1);
  
          // Move enemy
          enemy.position.addScaledVector(toDest, approachSpeed);
  
          // Ensure walk anim is playing
          if (enemy.userData.currentAnimation !== 'WalkForward') {
            playEnemyAnimation('WalkForward', true, enemy);
            enemy.userData.currentAnimation = 'WalkForward';
          }
        }
      } else {
        // If no destination, ensure idle
        if (enemy.userData.currentAnimation !== 'Idle') {
          playEnemyAnimation('Idle', true, enemy);
          enemy.userData.currentAnimation = 'Idle';
        }
      }
      updateEnemyAnimationsForEnemy(enemy, deltaTime);
    });
  }

  lerpAngle(a, b, t) {
    let delta = b - a;
    delta = ((delta + Math.PI) % (2 * Math.PI)) - Math.PI;
    return a + delta * t;
  }
}
